@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,400;0,500;0,700;1,400;1,500&display=swap);
body {
  color: white;
  margin: 0;
  font-family: 'Archivo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#181717;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2{
    margin: 0px;
    margin-left: 20px;
}

.button {

}

h1{
    margin: 0px;
    margin-right: 20px;
    color: #1ed760;
}

.logo {
    font-style: italic;
    font-weight: bold;
    letter-spacing: -0.04em;
    font-size: 48px;
}
.navbar-container{
    margin-top: 10px;
    margin-bottom: 10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
}

.pipe{
    border-right: 2px white solid;
}

.MuiButtonBase-root.MuiButton-root.MuiButton-contained.button.MuiButton-containedPrimary{
    margin-left: auto;
}
.table-head .MuiTableCell-sizeSmall {
    padding-bottom: 25px;
    padding-top: 0px;
    padding-left: 0px;
    padding-right: 0px;
}

h2 {
    margin: 0;
    font-weight: 500;
    margin-bottom: 16px;
}

.table-body .MuiTableCell-sizeSmall {
    padding: 0
}

.table-body .table-img {
    width: 1px;
}

.table-body .MuiTableCell-root {
    line-height: 0;
}

.table-body .MuiTableCell-root img {
    width: 64px;
    height: 64px;
}

.table-head th {
    color: #BFBFBF
}

.table-body .table-artist {
    font-style: italic;
}
.genres-container{
    display:flex;
    justify-content: center;
    align-items: center;
}
.container{
    position: relative;
    width: 100%;
    height: 100vh;
}

.container .section {
    position: relative;
    width: 100%;
    height: 100%;
}
html, body {
    height: 100%;
}

.NotLogged-Container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.NotLogged-Content {
    display: flex;
    height: 40%;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.logo.big {
    font-size: 130px;
}
